    bottom: 135px;
    width: 335px;
    padding: 20px;
  }
}
@media (max-width: 1024px) {
  .cookie {
    left: 10px;
    right: 10px;
    bottom: -1000px;
    padding: 20px;
    width: unset;
    align-items: unset;
  }
}
.cookie.active {
  left: 0;
}
@media (max-width: 1024px) {
  .cookie.active {
    left: 10px;
    bottom: 10px;
  }
}
.cookie .cookie-title {
  font-size: 30px;
  line-height: 30px;
}
@media (min-width: 1420px) and (max-width: 1920px) {
  .cookie .cookie-title {
    font-size: 1.563vw;
  }
}
@media (min-width: 1025px) and (max-width: 1419px) {
  .cookie .cookie-title {
    font-size: 20px;
  }
}
@media (max-width: 1024px) {
  .cookie .cookie-title {
    font-size: 20px;
    line-height: 20px;
  }
}
.cookie .cookie-description {
  margin-top: 20px;
  font-size: 15px;
  line-height: 18px;
}
