// Burger Layout Vars
$line-color:      $black;
$line-width:      30px;
$line-spacing:    22px;
$line-thickness:  2px;
// Burger Animation Vars
$transition-time: 0.4s;
$rotation:        45deg;
$translateY:      ($line-spacing / 2);
$translateX:      0;

$transition:     .2s ease-in-out;
$transitionSlow: .6s ease-in-out;

#hamburger-icon {
  width:          $line-width;
  height:         $line-spacing;
  display:        flex;
  flex-direction: column;
  align-items: 	flex-start;
  gap:            8px;
  text-align:     right;
  z-index:        6;
  right:          15px;
  top:            30px;
  cursor: pointer;
  transition:     top $transition;

  @include hover {
    & .line {
      background: $brown !important;
    }
  }

  & .line {
    display:       block;
    background:    $line-color;
    width:         $line-width;
    height:        $line-thickness;
    right:         0;
    border-radius: ($line-thickness / 2);
    transition:    all $transition-time;

    &.line-1 {
      top: 0;
    }

    &.line-2 {
      top: 50%;
    }

    &.line-3 {
      width: 100%;
      top: 100%;
    }
  }

  &.active {
    .line-1 {
      transform: translateY(10px) translateX($translateX) rotate($rotation);
      width:     100%;
    }

    .line-2 {
      opacity: 0;
      width:   100%;
    }

    .line-3 {
      transform: translateY(-10px) translateX($translateX) rotate($rotation * -1);
      width:     100%;
    }
  }
}